body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color:transparent;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#mescroll {
  position: fixed;
  top: 45px;
  bottom: 0;
  height: auto; /*如设置bottom:50px,则需height:auto才能生效*/
  max-width: 750px;
  -webkit-overflow-scrolling: touch;
}
.mescroll-totop {
  bottom: 70px !important;
}
.news-wrap{
  width: 100%;
  display: flex;
  display: -webkit-flex;
  padding: 15px 0px;
  min-height: 2rem;
  overflow: hidden;
  border-bottom: 1px solid #f3f3f3;
  background: #fff;
}
.news-wrap .img-wrap{
  width:33%;
  height: 2rem;
  margin-right: 10px;
}
.news-wrap .img-wrap div{
width: 100%;
background-color: rgba(150, 150, 150, 0.1);
background-repeat: no-repeat;
background-position: center;
background-size: cover;
height: 100%;
border-radius: 2px;
}
.news-wrap .img-wrap img{
  width: 100%;
  height: 100%;
}
.txt-wrap{
  width: 65%;
  height: 75px;
  position: relative;
}
.txt-wrap h3{
  font-size: 18px;
  line-height:1.3em;
  color: #222222;
  margin-bottom: 15px;
  font-weight: 800;
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: 2;
  word-wrap: break-word;
  word-break: break-all;
  text-overflow: ellipsis;
  
}
.txt-wrap p{
  width: 100%;
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-between;
}
.txt-wrap p .tag-view {
  padding-right: 2px; 
  font-family: PingFang-SC-Medium;
}
.txt-wrap .tags {
  position: absolute;
  bottom: 0;
  color: #999999;
  overflow: hidden;
  font-size: 14px;
  font-family: PingFang-SC-Medium;
}
.J-news{
padding: 15px 0px;
}
.news-item-s2{
width: 100%;
min-height: 75px;
overflow: hidden;
border-bottom: 1px solid #f3f3f3;
background: #fff;

}
.news-item-s2 h3 {
font-size: 18px;
line-height: 1.3em;
overflow: hidden;
width: 100%;
/* max-height: 40px; */
margin-bottom: 15px;
font-weight: 800;
color: #222222;
display: -webkit-box; 
  display: -moz-box;  
  white-space: pre-wrap; 
  word-wrap: break-word;
  overflow: hidden; 
  text-overflow: ellipsis; 
  -webkit-line-clamp:'2'; 
}
.news-item-s2  .img-wrap2{
display: flex;
display: -webkit-flex;
overflow: hidden;
height: 2rem;
width: 100%;
}
.img-wrap2 .img{
width: 33.33%;
padding-right: 2px;
}
.img-wrap2 .img div {
width: 100%;
background-color: rgba(150, 150, 150, 0.1);
background-repeat: no-repeat;
background-position: center;
background-size: cover;
height: 100%;
border-radius: 2px;
}
.img-wrap2 .img div img{
width: 100%;
height: 100%;
}
.J-news  .tags {
margin-top: 15px;
color: #999999;
overflow: hidden;
font-size: 12px;
font-family: PingFang-SC-Medium;
}
.J-news  .tags .tag {
display: inline-block;
line-height: 12px;
margin-right: 5px;
}
.J-news  .tags  .tag-view {
float: right;
margin-right: 0px;
}
#myCanvas{
  position: fixed;
  right:10px;
  bottom: 10px;
  z-index: 10000;
  font-size: 12px;
  border: 2px solid #007ed7;
  background: rgba(255, 255, 255, .6);
  border-radius: 50%;
  -webkit-border-radius: 50%;
  color: #002078;
}
