header {
    position: fixed;
    z-index: 10;
    top: 0;
    width: 10rem;
    max-width: 750px;
    height: 45px;
  }
  header  .top-menu {
    position: relative;
    height: 45px;
    background-color: #fff;
    overflow-y: hidden;
  }
  header  .top-menu:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-image: -webkit-linear-gradient(
      top,
      transparent,
      transparent 40%,
      #ddd 0
    );
    background-size: 100% 1px;
    background-position: bottom;
    background-repeat: no-repeat;
  }
  header  .top-menu-list {
    overflow: hidden;
    overflow-x: scroll;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
  }
  header  .top-menu-list  li {
    font-size: 18px;
    line-height: 35px;
    display: inline-block;
    height: 35px;
    margin: 0 5px 0 10px;
    padding: 5px 5px;
    -webkit-animation: fadeout 0.4s;
    animation: fadeout 0.4s;
    vertical-align: top;
    white-space: nowrap;
    color: #333;
    text-decoration: none;

  }
  header  .top-menu-list  .activeClass {
    color: #007dd5;
    font-size: 20px;
    position: relative;
    font-weight: bold;
  }
  header  .top-menu-list  .activeClass:after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    border-radius: 2px;
    left: 0;
    bottom: 0;
    background-color: #007dd5;
  }
