.artical {
    padding-top: 12px;  
    border-bottom: 1px solid #f3f3f3;
    overflow: hidden;
}
.artical:before {
    content: "";
    position: absolute;
    width: 187.5%;
    height: 1px;
    bottom: 0;
    left: 3%;
    right: 0;
    top: 0;
    margin: 0 auto;
    /* border-bottom: 1px solid #e6e6e6; */
    transform-origin: 0 0;
    transform: scale(0.5, 0.5);
    box-sizing: border-box;
  }
.artical h3 {
    font-size: 20px;
    color: #333;
    font-weight: bolder;
    line-height: 1.5;
    margin-bottom: 20px;
}
.artical  .source {
    font-size: 13px;
    color: #666;
    margin-bottom: 27px;
   
}
.artical  .source>span:first-child {
    margin-right: 15px;
}
.artical  .backhome-btn {
    background-color: transparent;
    width: 100%;
    height: 40px;
    border: 1px solid rgba(229, 4, 7, 1);
    border-radius: 4px;
    color: #e50407;
    font-size: 16px;
    margin: 20px 0;
  }
.artical  .more-list {
    font-size: 16px;
    line-height: 16px;
    font-weight: bold;
    color: #333333;
    text-indent: 7px;
    position: relative;
    
}
.artical  .more-list:after {
    content: "";
    display: block;
    width: 2px;
    height: 13px;
    background: #007dd5;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    
}
#content {
    word-wrap: break-word;
    font-size: 16px;
    color: #333;
    line-height: 30px;
}
#content p {
    word-wrap: break-word;
    font-size: 16px;
    color: #333;
    line-height: 30px;
    margin-bottom: 15px;
}
#content img {
    width: 100%;
    border-radius: 4px;
}
.artical  .copyright {
    margin-bottom: 10px;
    font-size: 18px;
}
.artical .pagination {
    display: flex;
    display: -webkit-flex;
    display: -moz-flex;
    justify-content: space-between;
    align-items: center;
}
.artical .pagination  button {
    border: 1px solid rgba(220, 220, 220, 1);
    border-radius: 2px;
    background-color: transparent;
    color: #999;
    font-size: 15px;
  }
.artical .pagination  .pageup,.artical .pagination .pagedown{
    max-width: 60px;
    min-width: 50px;
    min-height: 40px;
}
.artical .pagination .pageup {
    margin-right: 4.5px;
}
.artical .pagination  .pagedown {
    margin-left: 4.5px;
}
.artical .pagination .page-btn {
    /* min-width: 30px; */
    width: 40px;
    min-height: 40px;
    margin: 0 4.5px;
}
.artical .backhome-btn {
    background-color: transparent;
    width: 100%;
    height: 40px;
    border: 1px solid #007dd5;
    border-radius: 4px;
    color: #007dd5;
    font-size: 16px;
    margin: 20px 0;
}
.artical .pagination .page-btn.page-btn-active,
.artical .pagination .page-btn:active,
.artical .pagination .pagedown:active,
.artical .pagination .pageup:active {
  background-color: #007dd5;
  color: #fff;
  border: none;
}
.artical .pagination .ellipses-icon {
    margin: 0 4.5px;
  }
.more-list {
    font-size: 18px;
    line-height: 18px;
    font-weight: bold;
    color: #333333;
    text-indent: 7px;
    position: relative;
    /* margin-bottom: 10px; */
}
.more-list :after {
    content: "";
    display: block;
    width: 2px;
    height: 13px;
    background: #007dd5;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }